:host {
  display: block;
  position: fixed;
  top: 96px;
  left: 0;
  height: calc(100vh - 96px);
  width: 280px;
  z-index: 1001;
}

.store-left-menu {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: var(--glass-bg, var(--bg-primary));
  backdrop-filter: var(--glass-blur);
  -webkit-backdrop-filter: var(--glass-blur);
  border-right: 1px solid var(--tile-border, var(--border-color));
  box-shadow: 2px 0 20px var(--tile-shadow, var(--shadow));
  animation: slideInFromLeft 0.3s ease-out;

  // 인증 섹션 스타일
  .auth-section {
    padding: 16px;
    border-bottom: 1px solid var(--tile-border, var(--border-color));
    background: var(--tile-bg, var(--bg-card));

    .user-info {
      display: flex;
      align-items: center;
      gap: 12px;
      margin-bottom: 16px;

      .user-avatar {
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--primary-color);
        border-radius: 50%;
        color: white;

        mat-icon {
          font-size: 32px;
          width: 32px;
          height: 32px;
        }
      }

      .user-details {
        flex: 1;

        .user-name {
          font-weight: 600;
          color: var(--text-primary);
          font-size: 14px;
          margin-bottom: 4px;
        }

        .user-mode {
          font-size: 12px;
          color: var(--text-secondary);
          background: var(--bg-accent);
          padding: 2px 8px;
          border-radius: 12px;
          display: inline-block;
        }
      }
    }

    .login-section {
      .current-mode {
        margin-bottom: 12px;
        font-size: 12px;
        color: var(--text-secondary);
        text-align: center;
      }
    }

    .auth-actions {
      display: flex;
      flex-direction: column;
      gap: 8px;

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;
        padding: 10px 16px;
        border: none;
        border-radius: 8px;
        font-size: 14px;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.2s ease;
        background: var(--bg-secondary);
        color: var(--text-primary);

        &:hover {
          background: var(--bg-hover);
          transform: translateY(-1px);
        }

        &.primary {
          background: var(--primary-color);
          color: white;

          &:hover {
            background: var(--primary-dark);
          }
        }

        &.logout-btn {
          background: var(--error-light);
          color: var(--error-color);

          &:hover {
            background: var(--error-color);
            color: white;
          }
        }

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }
    }
  }

  .main-menu,
  .expanded-panel {
    height: 100%;
    overflow-y: auto;
  }

  .panel-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    position: sticky;
    top: 0;
    z-index: 10;

    h3 {
      flex: 1;
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      color: var(--text-primary);
    }

    .back-btn,
    .close-btn {
      background: none;
      border: none;
      color: var(--text-secondary);
      cursor: pointer;
      padding: 4px;
      border-radius: 4px;
      transition: all 0.2s ease;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        background: var(--bg-tertiary);
        color: var(--text-primary);
      }

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }
    }
  }

  .category-list {
    padding: 16px;

    .category-item {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 16px;
      border-radius: 12px;
      cursor: pointer;
      transition: all 0.2s ease;
      margin-bottom: 8px;
      border: 1px solid var(--tile-border, transparent);
      background: var(--tile-bg, transparent);

      &:hover {
        background: var(--bg-hover, var(--bg-secondary));
        border-color: var(--accent-primary, var(--border-color));
        transform: translateY(-1px);
        box-shadow: 0 4px 12px var(--tile-shadow, var(--shadow));
      }

      .category-icon {
        font-size: 32px;
        width: 48px;
        height: 48px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--gradient-primary);
        border-radius: 12px;
        flex-shrink: 0;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
      }

      .category-content {
        flex: 1;

        h4 {
          margin: 0 0 4px 0;
          font-size: 14px;
          font-weight: 600;
          color: var(--text-primary);
        }

        p {
          margin: 0;
          font-size: 12px;
          color: var(--text-secondary);
        }
      }

      .arrow-icon {
        color: var(--text-secondary);
        font-size: 20px;
        width: 20px;
        height: 20px;
        transition: transform 0.2s ease;
      }

      &:hover .arrow-icon {
        transform: translateX(4px);
        color: var(--accent-primary);
      }
    }
  }

  .menu-footer {
    position: sticky;
    bottom: 0;
    background: var(--glass-bg, var(--bg-primary));
    backdrop-filter: var(--glass-blur);
    -webkit-backdrop-filter: var(--glass-blur);
    border-top: 1px solid var(--tile-border, var(--border-color));
    padding: 16px;

    .quick-links {
      display: flex;
      gap: 8px;

      .quick-link {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 4px;
        background: var(--tile-bg, var(--bg-secondary));
        border: 1px solid var(--tile-border, var(--border-color));
        border-radius: 8px;
        padding: 12px 8px;
        cursor: pointer;
        transition: all 0.2s ease;
        color: var(--text-primary);

        &:hover {
          background: var(--accent-primary);
          color: white;
          transform: translateY(-1px);
          box-shadow: 0 4px 12px var(--tile-shadow);
        }

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
        }

        span {
          font-size: 11px;
          font-weight: 500;
          text-align: center;
        }
      }
    }
  }

  .sub-menu-list {
    padding: 16px;

    .sub-menu-item {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 12px 16px;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      margin-bottom: 4px;
      border-left: 3px solid transparent;
      background: var(--tile-bg, transparent);

      &:hover {
        background: var(--bg-hover, var(--bg-secondary));
        border-left-color: var(--accent-primary);
        transform: translateX(4px);
      }

      .sub-item-content {
        flex: 1;

        h4 {
          margin: 0 0 2px 0;
          font-size: 13px;
          font-weight: 500;
          color: var(--text-primary);
        }

        p {
          margin: 0;
          font-size: 11px;
          color: var(--text-secondary);
          line-height: 1.4;
        }
      }

      .sub-arrow {
        color: var(--text-secondary);
        font-size: 16px;
        width: 16px;
        height: 16px;
        transition: all 0.2s ease;
      }

      &:hover .sub-arrow {
        color: var(--accent-primary);
        transform: translateX(2px);
      }
    }
  }
}

@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

// 반응형 디자인
@media (max-width: 992px) {
  .store-left-menu {
    width: 280px;
    box-shadow: 4px 0 12px var(--shadow);
  }
}

@media (max-width: 768px) {
  :host {
    width: 80%;
    max-width: 280px;
  }

  .store-left-menu {
    .panel-header {
      padding: 12px 16px;

      h3 {
        font-size: 14px;
      }

      .back-btn,
      .close-btn {
        width: 28px;
        height: 28px;

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }
    }

    .category-list {
      padding: 12px;

      .category-item {
        padding: 12px;
        gap: 12px;

        .category-icon {
          font-size: 28px;
          width: 40px;
          height: 40px;
        }

        .category-content {
          h4 {
            font-size: 13px;
          }

          p {
            font-size: 11px;
          }
        }
      }
    }

    .menu-footer {
      padding: 12px;

      .quick-links .quick-link {
        padding: 8px 6px;

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }

        span {
          font-size: 10px;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  :host {
    top: 80px;
    height: calc(100vh - 80px);
  }
}
// ==========================================
// 모바일 최적화 (768px 이하)
// ==========================================
@media (max-width: 768px) {
  :host {
    top: 56px;
    height: calc(100vh - 56px);
    width: 100%;
    z-index: 1002;
  }

  .store-left-menu {
    width: 85%;
    max-width: 320px;
    box-shadow: 4px 0 16px rgba(0, 0, 0, 0.3);
  }

  // 인증 섹션
  .auth-section {
    padding: 20px 16px;

    .user-info {
      margin-bottom: 20px;

      .user-avatar {
        width: 56px;
        height: 56px;

        mat-icon {
          font-size: 36px;
          width: 36px;
          height: 36px;
        }
      }

      .user-details {
        .user-name {
          font-size: 16px;
        }

        .user-mode {
          font-size: 13px;
          padding: 4px 10px;
        }
      }
    }

    .auth-actions {
      gap: 10px;

      .mode-switch-btn,
      .logout-btn,
      .login-btn {
        padding: 10px 16px;
        font-size: 14px;

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
        }
      }
    }
  }

  // 메인 메뉴
  .main-menu {
    .panel-header {
      padding: 20px 16px;

      h3 {
        font-size: 18px;
      }

      .close-btn {
        padding: 8px;

        mat-icon {
          font-size: 26px;
          width: 26px;
          height: 26px;
        }
      }
    }

    .category-list {
      padding: 12px;

      .category-item {
        padding: 16px;
        margin-bottom: 12px;

        .category-icon {
          font-size: 32px;
          width: 48px;
          height: 48px;
        }

        .category-content {
          h4 {
            font-size: 16px;
          }

          p {
            font-size: 13px;
          }
        }

        .arrow-icon {
          font-size: 26px;
          width: 26px;
          height: 26px;
        }
      }
    }

    .menu-footer {
      padding: 12px;

      .quick-links {
        gap: 10px;

        .quick-link {
          padding: 14px;

          mat-icon {
            font-size: 22px;
            width: 22px;
            height: 22px;
          }

          span {
            font-size: 14px;
          }
        }
      }
    }
  }

  // 확장 패널
  .expanded-panel {
    .panel-header {
      padding: 20px 16px;

      h3 {
        font-size: 18px;
      }

      .back-btn,
      .close-btn {
        padding: 8px;

        mat-icon {
          font-size: 26px;
          width: 26px;
          height: 26px;
        }
      }
    }

    .sub-menu-list {
      padding: 12px;

      .sub-menu-item {
        padding: 16px;
        margin-bottom: 10px;

        .sub-item-content {
          h4 {
            font-size: 15px;
          }

          p {
            font-size: 13px;
          }
        }

        .sub-arrow {
          font-size: 24px;
          width: 24px;
          height: 24px;
        }
      }
    }
  }
}

// 모바일 오버레이 배경
@media (max-width: 768px) {
  :host::before {
    content: '';
    position: fixed;
    top: 56px;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: -1;
    animation: fadeIn 0.3s ease-out;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// ==========================================
// 초소형 모바일 최적화 (480px 이하)
// ==========================================
@media (max-width: 480px) {
  .store-left-menu {
    width: 90%;
    max-width: 280px;
  }

  .auth-section {
    padding: 16px 12px;

    .user-info {
      .user-avatar {
        width: 48px;
        height: 48px;

        mat-icon {
          font-size: 32px;
          width: 32px;
          height: 32px;
        }
      }

      .user-details {
        .user-name {
          font-size: 15px;
        }

        .user-mode {
          font-size: 12px;
          padding: 3px 8px;
        }
      }
    }
  }

  .main-menu {
    .panel-header {
      padding: 16px 12px;

      h3 {
        font-size: 17px;
      }
    }

    .category-list {
      padding: 8px;

      .category-item {
        padding: 14px;

        .category-icon {
          font-size: 28px;
          width: 44px;
          height: 44px;
        }
      }
    }
  }
}
